import React, { useState } from 'react';
import { Plus, Trash2, Phone, MapPin } from 'lucide-react';
import * as Types from '@/lib/types';
import * as Utils from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Modal } from '@/components/ui/modal';
import { useToast } from '@/components/providers/toast-provider';

interface TeachersViewProps {
    teachers: Types.Teacher[];
    onAdd: (t: Types.Teacher) => void;
    onDelete: (id: string) => void;
}

export const TeachersView: React.FC<TeachersViewProps> = ({ teachers, onAdd, onDelete }) => {
    const [showModal, setShowModal] = useState(false);
    const [formData, setFormData] = useState({ name: '', email: '', phone: '', address: '' });
    const { addToast } = useToast();
    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        onAdd({ ...formData, id: Utils.generateId(), created_at: Date.now(), updated_at: Date.now() });
        addToast('Teacher added successfully', 'success');
        setShowModal(false); setFormData({ name: '', email: '', phone: '', address: '' });
    };
    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center"><div><h1 className="text-2xl font-bold text-gray-900">Teachers Directory</h1><p className="text-gray-500">Manage academic staff</p></div><Button onClick={() => setShowModal(true)}><Plus className="h-4 w-4 mr-2" /> Add Teacher</Button></div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {teachers.map(t => (
                    <Card key={t.id} className="flex flex-col justify-between">
                        <div className="flex items-start justify-between mb-4">
                            <div className="flex items-center gap-3"><div className="h-12 w-12 rounded-full bg-purple-100 text-purple-600 flex items-center justify-center font-bold text-lg">{t.name.charAt(0)}</div><div><h3 className="font-bold text-gray-900">{t.name}</h3><span className="text-xs bg-purple-50 text-purple-700 px-2 py-0.5 rounded-full">Teacher</span></div></div>
                            <button onClick={() => onDelete(t.id)} className="text-gray-400 hover:text-red-600 transition-colors"><Trash2 className="h-4 w-4" /></button>
                        </div>
                        <div className="space-y-2 text-sm text-gray-600"><div className="flex items-center gap-2"><Phone className="h-3 w-3" /> {t.phone}</div><div className="flex items-center gap-2"><div className="h-3 w-3 flex items-center justify-center">@</div> {t.email}</div><div className="flex items-center gap-2"><MapPin className="h-3 w-3" /> {t.address}</div></div>
                    </Card>
                ))}
            </div>
            <Modal isOpen={showModal} onClose={() => setShowModal(false)} title="Add New Teacher">
                <form onSubmit={handleSubmit} className="space-y-4">
                    <Input label="Full Name" required value={formData.name} onChange={e => setFormData({ ...formData, name: e.target.value })} />
                    <div className="grid grid-cols-2 gap-4"><Input label="Phone Number" required value={formData.phone} onChange={e => setFormData({ ...formData, phone: e.target.value })} /><Input label="Email Address" type="email" value={formData.email} onChange={e => setFormData({ ...formData, email: e.target.value })} /></div>
                    <Input label="Residential Address" value={formData.address} onChange={e => setFormData({ ...formData, address: e.target.value })} />
                    <Button type="submit" className="w-full">Save Teacher</Button>
                </form>
            </Modal>
        </div>
    );
};
